import React from 'react';

/**
 * HTML     
 */
function stripHtmlTags(html: string): string {
  // <br>  (\n) 
  const withLineBreaks = html.replace(/<br\s*\/?>/gi, '\n');
  //  HTML  
  return withLineBreaks.replace(/<\/?[^>]+(>|$)/g, '');
}

//     (HTML  )
