import React from 'react';
import styled from 'styled-components';
import { useTranslation } from 'react-i18next';
import { motion } from 'framer-motion';

//    
interface NewsItem {
  id: number;
  title: string;
  date: string;
  content: string;
  image?: string;
}

//   
const News = () => {
  const { t } = useTranslation();

  //    (    )
  const newsItems: NewsItem[] = [
    {
      id: 1,
      title: t('news.item1.title'),
      date: '2024-01-15',
      content: t('news.item1.content'),
      image: '/images/news1.jpg'
    },
    {
      id: 2,
      title: t('news.item2.title'),
      date: '2024-01-10',
      content: t('news.item2.content')
    }
  ];

  return (
    <Container>
      <ContentWrapper>
        {/*    */}
        <TitleSection
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          <PageTitle>{t('news.pageTitle')}</PageTitle>
          <Subtitle>{t('news.subtitle')}</Subtitle>
        </TitleSection>

        {/*    */}
        <NewsSection>
          {newsItems.map((item, index) => (
            <NewsCard
              key={item.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.6, delay: index * 0.2 }}
            >
              {item.image && (
                <NewsImage>
                  <img src={item.image} alt={item.title} />
                </NewsImage>
              )}
              <NewsContent>
                <NewsTitle>{item.title}</NewsTitle>
                <NewsDate>{item.date}</NewsDate>
                <NewsText>{item.content}</NewsText>
                <ReadMoreButton>{t('news.readMore')}</ReadMoreButton>
              </NewsContent>
            </NewsCard>
          ))}
        </NewsSection>
      </ContentWrapper>
    </Container>
  );
};

//   
const Container = styled.div`
  width: 100%;
  min-height: 100vh;
  background-color: #ffffff;
  padding: 40px 0;
`;

const ContentWrapper = styled.div`
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
`;

//   
const TitleSection = styled(motion.div)`
  text-align: center;
  margin-bottom: 60px;
`;

const PageTitle = styled.h1`
  font-size: 2.5rem;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 16px;
`;

const Subtitle = styled.p`
  font-size: 1.1rem;
  color: #666666;
  line-height: 1.6;
`;

//   
const NewsSection = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
`;

//   
const NewsCard = styled(motion.article)`
  background-color: #ffffff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease;

  &:hover {
    transform: translateY(-5px);
  }
`;

//   
const NewsImage = styled.div`
  width: 100%;
  height: 300px;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
  }

  ${NewsCard}:hover & img {
    transform: scale(1.05);
  }
`;

//   
const NewsContent = styled.div`
  padding: 24px;
`;

//   
const NewsTitle = styled.h2`
  font-size: 1.5rem;
  font-weight: 600;
  color: #1a1a1a;
  margin-bottom: 12px;
`;

//   
const NewsDate = styled.p`
  font-size: 0.9rem;
  color: #666666;
  margin-bottom: 16px;
`;

//   
const NewsText = styled.p`
  font-size: 1rem;
  color: #4a4a4a;
  line-height: 1.6;
  margin-bottom: 24px;
`;

//   
const ReadMoreButton = styled.button`
  background: none;
  border: 2px solid #1a1a1a;
  border-radius: 6px;
  padding: 8px 16px;
  font-size: 0.9rem;
  font-weight: 600;
  color: #1a1a1a;
  cursor: pointer;
  transition: all 0.2s ease;

  &:hover {
    background-color: #1a1a1a;
    color: #ffffff;
  }
`;

export default News;
